<template>
	<view class="register">
		<view class="formBox">
			<view class="mititle">
				<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
				基本身份信息
			</view>
			<view class="mybox">
				<u-avatar src="/static/img/banner.png" size="100"></u-avatar>
			</view>
			<u-cell-group class="tsbox">
				<u-cell-item title="姓名" :value="details.universalAntiUserMain27" :arrow="false"></u-cell-item>
				<u-cell-item title="性别" :value="details.sex==1?'男':'女'" :arrow="false"></u-cell-item>
				<u-cell-item title="职务" :value="details.orleFid" :arrow="false"></u-cell-item>
				<u-cell-item title="联系电话" :value="details.phone" :arrow="false"></u-cell-item>
				<u-cell-item title="活动分值" :value="'A：'+details.scoreA+' B:'+details.scoreB" :arrow="false"></u-cell-item>
				<u-cell-item title="身份证号" :value="details.idcard" :arrow="false"></u-cell-item>
				<u-cell-item title="单位性质" :value="fycon(details.unitStype)" :arrow="false"></u-cell-item>
				<u-cell-item title="单位名称" :value="details.unitName" :arrow="false"></u-cell-item>
				<u-cell-item title="政治面貌" :value="fytitle(details.politicalOutlook)" :arrow="false"></u-cell-item>
				<u-cell-item title="居住地" :value="details.place+details.placeAddress" :arrow="false"></u-cell-item>
				<u-cell-item title="其他联系电话" :value="details.otherPhone" :arrow="false"></u-cell-item>
				<u-cell-item title="文化程度" :value="fydd(details.degree)" :arrow="false"></u-cell-item>
				<u-cell-item title="特长" :value="details.skilled" :arrow="false"></u-cell-item>
				<u-cell-item title="专业" :value="details.specializedSubject" :arrow="false"></u-cell-item>
				<u-cell-item title="爱好" :value="details.like" :arrow="false"></u-cell-item>
			</u-cell-group>
		</view>
		<view class="formBox" style="margin-top: 20rpx;">
			<view class="mititle">
				<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
				活动信息
			</view>
			<u-cell-group>
				<u-cell-item :title="item.universalAntiAboutMain26" :value="item.createTime" :arrow="false" v-for="(item,index) in active" :key="index"></u-cell-item>

			</u-cell-group>
		</view>
	</view>
</template>

<script>
	import {
		getDicts,
		getInfo,
		enterAbout
	} from '../../api/index.js'
	export default {
		data() {
			return {
				details: {},
				zdlist: [],
				tylist: [],
				degree: [],
				active:[],
			}
		},
		onLoad() {
			getDicts("political_outlook").then(res => {
				if (res.code == 200) {
					this.zdlist = res.data;
				}
			})
			getDicts("unit_stype").then(res => {
				if (res.code == 200) {
					this.tylist = res.data;
				}
			})
			getDicts("degree").then(res => {
				if (res.code == 200) {
					this.ddlist = res.data;
				}
			})
			getInfo({id:this.$route.query.id}).then(res => {
				if (res.code == 200) {
					this.details = res.data;
				}
			})
			enterAbout({id:this.$route.query.id}).then(res => {
				if (res.code == 200) {
					this.active = res.data;
				}
			})
		},
		methods: {
			fytitle(name) {
				let result = null;
				this.zdlist.map((item) => {
					if (item.dictValue == name) {
						result = item.dictLabel;
					}
				})
				return result;
			},
			fycon(name) {
				let result = null;
				this.tylist.map((item) => {
					if (item.dictValue == name) {
						result = item.dictLabel;
					}
				})
				return result;
			},
			fydd(name) {
				let result = null;
				this.ddlist.map((item) => {
					if (item.dictValue == name) {
						result = item.dictLabel;
					}
				})
				return result;
			},
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
	}

	.mybox {
		text-align: center;
		margin: 50rpx 0;
	}

	.formBox {
		padding: 0 30rpx 30rpx 30rpx;
		background-color: #fff;
	}

	.mititle {
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		border-bottom: 1px solid #f1f1f1;
	}

	.tsbox /deep/ .u-cell_title {
		color: #777777;
	}

	.tsbox /deep/ .u-cell__value {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
	}
</style>